<template>
  <div class="c-course-calender">
    <div class="c-course-calender-wrapper" ref="calendarWrapper">
      <table border="0" frame="border">
        <thead>
          <tr>
            <td>
              <div class="date-time-head">
                <span></span>
                <span>/</span>
              </div>
            </td>
            <td v-for="w in weeks" :key="w.date">
              <div>{{w.title}}</div>
              <div>{{w.date}}</div>
            </td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="i in times" :key="i.t">
            <td :class="{time: index !== 0 && canAdd}" @click="handleTap(i, index, $event)" v-for="(u, index) in [1,2,3,4,5,6,7,8]" :key="u">
              {{ index === 0 ? i.t : '' }}
            </td>
          </tr>
        </tbody>
      </table>
      <div
        class="dot"
        v-for="course in courses"
        :key="course.startTime"
        @click.stop="handleTapItem(course)"
        :style="[{ top: course.top, height: course.height, left: course.left }]">
        <div class="inner">
          <span>{{course.name}}</span>
          <span>{{course.classroom}}</span>
          <span>{{course.time}}</span>
          <span>{{course.nums}}</span>
          <i class="el-icon-edit"></i>
        </div>
      </div>
    </div>
    <slot />
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { getPoint } from '@/utils/position'
import { getWeek, weekNames, now } from '@/utils/day.js'

const min = 60 * 1000
const hour = 60 * min

export default {
  name: 'CCourseCalendar',
  props: {
    canAdd: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      courses: []
    }
  },
  computed: {
    times () {
      let start = 8
      const end = 22
      const times = []
      while (start <= end) {
        const h = start < 10 ? `0${start}` : start
        times.push({ t: `${h}:00` })
        start += 1
      }
      return times
    },
    currentDate () {
      return now
    },
    weeks () {
      const weeks = getWeek(this.currentDate)
      return weeks.map((w, index) => {
        return {
          title: weekNames[index],
          date: w.format('M.DD')
        }
      })
    }
  },
  mounted () {
    this.$nextTick(() => {
      const wrapper = this.$refs.calendarWrapper
      const width = wrapper.getBoundingClientRect().width
      wrapper.style.width = width - ((width - 9) % 8) + 'px'
    })
    this.parseCourses()
  },
  methods: {
    parseCourses () {
      const data = [{
        name: '',
        startTime: 1613606400000,
        endTime: 1613613600000,
        classroom: 'L2-201',
        nums: '50'
      }, {
        name: '',
        startTime: 1613786400000,
        endTime: 1613795400000,
        classroom: 'L2-201',
        nums: '50'
      }]
      this.courses = data.map(item => {
        const { startTime, endTime } = item
        const offset = (endTime - startTime) / hour
        const startDate = dayjs(startTime)
        const day = startDate.day()
        const top = (startTime - dayjs(`${startDate.format('YYYY-MM-DD') + ' 08:00:00'}`).valueOf()) / hour + 1
        return {
          ...item,
          time: `${startDate.format('HH:mm')} - ${dayjs(endTime).format('HH:mm')}`,
          left: `calc((100% - 9px) / 8 * ${day} + 1px + (${day}px + 1px))`,
          top: `${top * 60 + top + 1}px`,
          height: `${offset * 60 + offset - 1}px`
        }
      })
    },
    handleTapItem (course) {
      this.$emit('tapCourse', course)
    },
    handleTap (i, index, e) {
      if (index !== 0 && this.canAdd) {
        const point = getPoint(e.target, this.$refs.calendarWrapper)
        point.top += e.offsetY
        point.left += e.offsetX
        this.$emit('tapDate', { point, item: i, index })
      }
    }
  }
}
</script>

