    border 1px solid blue
  .dot
    position absolute
    width calc((100% - 9px) / 8 - 2px)
    padding 2px 0
    box-sizing border-box
    .inner
      width 100%
      height 100%
      background-color red
      border-radius 10px
      display flex
      padding 10px
      box-sizing border-box
      flex-direction column
      position relative
