<template>
  <router-view v-if="hasSubRoute" />
  <div v-else class="layout-content-wrapper">
    <top-breadcrumb :items="['']" />
    <div class="layout-content-main">
      <div class="wrapper">
        <custom-form
          inline
          :items="finishedConfig.formOptions"
        />
        <custom-table
          :options="finishedConfig.tableOptions"
          :data="data"
          :page-index="pageIndex"
          :page-size="pageSize"
          @tableColumnEvent="handleTalbeColumnAction"
        />
        <pagination :page-size="pageSize" :current="pageIndex" :total="100" @current-change="currentChange" />
      </div>
    </div>
  </div>
</template>

<script>
import useList from '@/hooks/useList'
import { finishedConfig } from '@/config/course'
export default {
  name: 'VFinishedCourse',
  setup () {
    const listData = useList(() => {
      return Promise.resolve([
        { name: 'name' },
        { name: 'name1', actions: [{ title: '', event: 'feedback' }, { title: '', event: 'progress' }, { title: '', event: 'detail' }] }
      ])
    })

    return {
      finishedConfig,
      ...listData
    }
  },
  methods: {
    handleTalbeColumnAction (newValue, options) {
      const { type, action } = options
      if (type === 'action') {
        if (action.event === 'assignHomework') {
          this.$router.push({ name: 'AssignHomework' })
        } else if (action.event === 'progress') {
          this.$router.push({ name: 'CourseHomework' })
        }
      }
    }
  }
}
</script>
