import dayjs from 'dayjs'

export const now = dayjs(dayjs().format('YYYY-MM-DD'))
export const startOfWeek = getStartOfWeek(now)
export const end = now.add(3, 'month').subtract(1, 'day')
export const weekNames = ['', '', '', '', '', '', '']

export function getStartOfWeek (date) {
  const day = (date.day() || 7) - 1
  return date.subtract(day, 'day')
}

export function getWeek (date = now) {
  const one = getStartOfWeek(date)
  const arr = [one]
  for (let index = 1; index < 7; index++) {
    arr.push(one.add(index, 'day'))
  }
  return arr
}

export function isSameDay (l, r) {
  if (!l || !r) return false
  return l.isSame(r)
}
