<template>
  <router-view v-if="hasSubRoute" />
  <div v-else class="layout-content-wrapper course-plan-detail-page">
    <top-breadcrumb :items="['', '']" showBack @back="$router.routeBack()" />
    <div class="layout-content-main">
      <div class="wrapper">
        <GridInfo title="" :infos="courseInfos" />
        <div class="course-plan-detail-page-calendar">
          <h3></h3>
          <calendar canAdd @tapCourse="handleTapCalendar" @tapDate="handleShowAddPicker">
            <div ref="addPickerRef" class="course-plan-detail-page-add-picker">
              <div @click.stop="handleAdd(1)"></div>
              <div @click.stop="handleAdd(2)"></div>
            </div>
          </calendar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { useRoute } from '@/hooks/useRouter'
import GridInfo from '@/components/GridInfo'
import { onClickOutside } from '@vueuse/core'
import Calendar from '@/components/CourseCalendar'
import createComponent from '@/utils/createComponent'
import AddCurriculum from '../../components/AddCurriculum'

export default {
  name: 'VPlanDetail',
  components: { Calendar, GridInfo },
  setup () {
    const route = useRoute()
    const addPickerRef = ref(null)
    const infos = [
      { title: '', key: '1', value: 'Miss Ballet' },
      { title: '', key: '2', value: '' },
      { title: '', key: '3', value: '' },
      { title: '', key: '4', value: '48' },
      { title: '', key: '5', value: 'Miss Ballet' },
      { title: '', key: '6', value: 'Miss Ballet' },
      { title: '', key: '7', value: 'Miss Ballet' },
      { title: '', key: '8', value: 'Miss Ballet' }
    ]
    const handleAdd = (type) => {
      addPickerRef.value.style.display = 'none'
      createComponent(AddCurriculum)({ type }).$show()
    }
    const handleShowAddPicker = ({ point }) => {
      const picker = addPickerRef.value
      picker.style.display = 'flex'
      picker.style.top = `${point.top}px`
      picker.style.left = `${point.left}px`
    }

    onClickOutside(addPickerRef, () => {
      addPickerRef.value.style.display = 'none'
    })

    return {
      handleAdd,
      addPickerRef,
      courseInfos: infos,
      id: route.params.id,
      handleShowAddPicker
    }
  },
  methods: {
    handleTapCalendar (course) {
      this.$router.push({ name: 'CurriculumDetail', params: { id: '321' } })
    }
  }
}
</script>

<style lang="stylus" scoped>
.course-plan-detail-page
  &-calendar
    h3
      margin 10px 0 20px
  &-add-picker
    position absolute
    width 140px
    height 100px
    border-radius 10px
    box-sizing border-box
    font-size 14px
    background-color #F2F2F2
    display none
    flex-direction column
    align-items center
    justify-content center
    & > div
      cursor pointer
      padding 5px 10px
      &:first-child
        border-bottom 1px solid #CCCCCC
</style>
