import Vue from 'vue'

const createComponent = function (component) {
  const Constructor = Vue.extend(component)
  return function (props = {}, events = {}) {
    // parent 
    // routerstorerootVuewindow.rootApp
    // $parent  event clourse
    // let options = { parent }
    //  vue-create-api
    const instance = new Constructor()
    let _resolve, _reject
    let closed = false

    // traverse events
    Object.keys(props).forEach(key => {
      instance[key] = props[key]
    })

    instance.$mount()
    document.body.appendChild(instance.$el)

    // show close
    instance.$show = _ => {
      // !isRoot
      // Avoid mutating a prop directly
      instance.show = true
      return new Promise((resolve, reject) => {
        _resolve = resolve
        _reject = reject
      })
    }
    instance.$close = _ => {
      if (closed) return
      closed = true
      instance.show = false
      setTimeout(() => {
        instance.$el.remove()
        instance.$destroy()
      }, 300)
    }

    // on closesuccess event
    instance.$on('close', _ => {
      if (closed) return
      _reject(instance)
      instance.$close()
    })

    instance.$on('success', _ => {
      _resolve(instance)
      instance.$close()
    })

    // traverse events
    Object.keys(events).forEach(key => {
      instance.$on(key, _ => {
        events[key](instance, _)
      })
    })

    return instance
  }
}

export default createComponent
