<template>
  <div class="vc-curriculum-add">
    <h-dialog :custom-class="customClass" :show="show" :title="title" @close="$emit('close')">
      <div class="vc-curriculum-add-content">
        <generate-form
          ref="generateForm"
          label-width="100px"
          :items="options"
          :buttons="buttons"
          :models="models"
          @cancel="$emit('close')"
        >
          <template v-if="type === 2">
            <div slot="date">
              <el-date-picker
                type="daterange"
                size="mini"
                v-model="models.dates"
                range-separator=""
                start-placeholder=""
                end-placeholder="">
              </el-date-picker>
            </div>
            <div slot="week" class="week-wrapper">
              <div @click="handleTapWeek(index)" :class="{selected: models.weeks.includes(index)}" v-for="(w, index) in ['', '', '', '', '', '', '']" :key="index" >{{w}}</div>
            </div>
          </template>
          <div slot="time">
            <el-date-picker
              v-if="type === 1"
              class='custom-date-picker'
              v-model="models.date"
              size="mini"
              :editable="false"
              :clearable="false"
              type="date"
              placeholder="">
            </el-date-picker>
            <time-selector />
          </div>
          <div slot="student">student</div>
        </generate-form>
      </div>
    </h-dialog>
  </div>
</template>

<script>
import visibilityMixin from '@/mixins/visibility'
import TimeSelector from '@/components/time-picker/TimeSelector'

export default {
  name: 'VcCourseNew',
  mixins: [visibilityMixin],
  components: { TimeSelector },
  props: {
    type: {
      type: Number,
      default: 1 // 1 2 
    }
  },
  computed: {
    title () {
      return this.type === 1 ? '' : ''
    },
    customClass () {
      return 'vc-curriculum-add-dialog' + (this.type === 1 ? ' single' : ' period')
    },
    options () {
      const bases = [
        { label: '', type: 'EMPTY', model: 'time' },
        { label: '', type: 'SELECT', model: 'name' },
        { label: '', type: 'SELECT', model: 'name' },
        { label: '', type: 'SELECT', model: 'name' },
        { label: '', type: 'EMPTY', model: 'student' }
      ]
      if (this.type === 2) {
        bases.unshift({ label: '', type: 'EMPTY', model: 'date' })
        bases.splice(5, 0, { label: '', type: 'EMPTY', model: 'week' })
      }
      return bases
    }
  },
  data () {
    return {
      buttons: [
        { title: '', emit: 'cancel', type: ' ' },
        { title: '', emit: 'submit', type: 'primary' }
      ],
      models: {
        date: '',
        time: '',
        dates: '',
        weeks: [],
        teachers: []
      }
    }
  },
  methods: {
    handleTapWeek (index) {
      const weeks = this.models.weeks
      if (weeks.includes(index)) {
        this.models.weeks = weeks.filter(i => i !== index)
      } else {
        weeks.push(index)
      }
    }
  }
}
</script>

