  &-content
    display flex
    flex-direction column
    align-items center
    > button
      width 204px
  &-show-tip
    font-size 12px
    color #999999
  .week-wrapper
    display flex
    border 1px solid #EEEEEE
    & > div
      cursor pointer
      border 1px solid #EEEEEE
      padding 0 10px
      &.selected
        border-color red
</style>
